"use client";
import { createUserDocument, getConfig } from "./../actions/create";
import { initializeApp } from "firebase/app";
import { getAuth,GoogleAuthProvider,signInWithPopup} from "firebase/auth";
import { getFirestore } from "firebase/firestore";

function SignIn() {

  async function createUserAccount() {
    const firebaseConfig = await getConfig();
    const app = initializeApp(firebaseConfig);
    const auth = getAuth(app);
    const db = getFirestore(app);
    const googleProvider = new GoogleAuthProvider();
    try {
      const result = await signInWithPopup(auth, googleProvider);
      const credential = GoogleAuthProvider.credentialFromResult(result);
      const idToken = credential?.idToken;
      const user = result.user;
      const userData = {
        sub: user.uid, // Firebase UID used as the document ID
        fullName: user.displayName || "",
        email: user.email || "",
        email_verified: user.emailVerified,
        picture: user.photoURL || "",
        subscription_status: "Free",
        user_created_at: new Date(),
      };
      await createUserDocument(db, userData);
      // Generate a one time code
      // const code = crypto.randomBytes(16).toString("hex");

      if (window !== undefined) {
        window.location.href = "pingprospect://" + idToken;
      }
      return idToken;
    } catch (error) {
      console.error("Sign-in error:", error);
      throw error;
    }
  }
  return (
    <div className="flex min-h-screen w-full items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-50 p-4">
      <div className="w-full max-w-md rounded-2xl bg-white p-8 shadow-xl">
        <h2 className="mb-8 text-center text-3xl font-bold text-gray-800">
          Welcome to PingProspect
        </h2>
        <button
          onClick={createUserAccount}
          className="w-full rounded-lg bg-[#357AFF] px-4 py-3 text-base font-medium text-white transition-colors hover:bg-[#2E69DE] focus:outline-none focus:ring-2 focus:ring-[#357AFF] focus:ring-offset-2 disabled:opacity-50"
        >
          <span> SignIn with Google</span>
        </button>

        {/* {isLoggedIn ? (
          <button
          onClick={SiginOut}
          className="w-full rounded-lg bg-[#357AFF] px-4 py-3 text-base font-medium text-white transition-colors hover:bg-[#2E69DE] focus:outline-none focus:ring-2 focus:ring-[#357AFF] focus:ring-offset-2 disabled:opacity-50"
        > 
           <span className="text-base">Signout</span>
        </button>
        ) : (
          <button
            onClick={createUserAccount}
            className="w-full rounded-lg bg-[#357AFF] px-4 py-3 text-base font-medium text-white transition-colors hover:bg-[#2E69DE] focus:outline-none focus:ring-2 focus:ring-[#357AFF] focus:ring-offset-2 disabled:opacity-50"
          > 
             <span> SignIn with Google</span>
          </button>
        )} */}
      </div>
    </div>
  );
}

export default SignIn;
